import React, { Component } from "react";
import Tabs from "./components/Tabs"; 
import "./App.css";
import { useState } from "react";
import { ReactSketchCanvas } from "react-sketch-canvas";
import GoogleMap from "./components/GoogleMap";
import DashBoard from "./components/DashBoard";
import ListItems from './ListItems'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faTrash } from '@fortawesome/free-solid-svg-icons'


function App() {

  const [name, setName] = useState("SGT __");

  const styles = {
  border: "0.0625rem solid #9c9c9c",
  borderRadius: "0.10rem"};

  const Events = class extends React.Component {
    constructor(props){
    super(props);
    this.state = {
      items:[],
      currentItem:{
        text:'',
        key:''
      }
    }
    this.addItem = this.addItem.bind(this);
    this.handleInput = this.handleInput.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.setUpdate = this.setUpdate.bind(this);
  }
  addItem(e){
    e.preventDefault();
    const newItem = this.state.currentItem;
    if(newItem.text !==""){
      const items = [...this.state.items, newItem];
    this.setState({
      items: items,
      currentItem:{
        text:'',
        key:''
      }
    })
  }
}
  handleInput(e){
    this.setState({
      currentItem:{
        text: e.target.value,
        key: Date.now()
      }
    })
  }
  deleteItem(key){
    const filteredItems= this.state.items.filter(item =>
      item.key!==key);
    this.setState({
      items: filteredItems
    })

  }
  setUpdate(text,key){
    console.log("items:"+this.state.items);
    const items = this.state.items;
    items.map(item=>{      
      if(item.key===key){
        console.log(item.key +"    "+key)
        item.text= text;
      }
    })
    this.setState({
      items: items
    })
    
   
  }
 render(){
  return (
    <div className="App">
      <header>
        <form id="to-do-form" onSubmit={this.addItem}>
          <input type="text" placeholder="(00:00) Event Description" value= {this.state.currentItem.text} onChange={this.handleInput}></input>
          <button type="submit">Add</button>
        </form>
        <p>{this.state.items.text}</p>
        
          <ListItems items={this.state.items} deleteItem={this.deleteItem} setUpdate={this.setUpdate}/>
        
      </header>
    </div>
  );
 }
}


  const Canvas = class extends React.Component {
  constructor(props) {
    super(props);

    this.canvas = React.createRef();
  }

  render() {
    return (
      <div>
        <ReactSketchCanvas
          ref={this.canvas}
          style={styles}
          width= {1550}
          height= {1550}
          strokeWidth={4}
          strokeColor="black"
        />

        <button
          onClick={() => {
            this.canvas.current.eraseMode(true)
          }}
        >
          Erase
        </button>

        <button
          onClick={() => {
            this.canvas.current.eraseMode(false)
          }}
        >
          Pen
        </button>

        <button
          onClick={() => {
            this.canvas.current.undo()
          }}
        >
          Undo
        </button>

        <button
          onClick={() => {
            this.canvas.current.resetCanvas()
          }}
        >
          Reset
        </button>

      </div>
    );
  }
};

  return (
    <div>
          <h1>smartBACO</h1>
      <p>
        Welcome,{" "}
        <strong
          role="button"
          onClick={() => {
            const newName = prompt("What is your name?", name);
            setName(newName);
          }}
        >
          <button>{name || "<set a name>"}</button>
        </strong>
        !
      </p>

     <Tabs> 
       <div label="FireFighters"> 
         <DashBoard />
       </div> 


       <div label="Map"> 
         <GoogleMap />
       </div> 

       <div label="Events"> 
       <Events/>
       </div>

       <div label="Site Planning"> 
       <h3> Sketch </h3>
       <Canvas />
       </div> 
     </Tabs> 
    </div>
  );
}

export default App;